.card-container{
    display: flex;
    flex-wrap: wrap;
    margin-top: 2rem;
    margin-left: 20rem;
    z-index: -2;
}

.card{
    padding: 20px;
    margin: 20px;
    border: 2px solid #ededed;
    cursor: pointer;
}

.card-img{
    width: 13rem;
    margin-bottom:1rem;

}
.card-title{
    margin-bottom:1rem;
}
.card-reviews{
    margin-bottom:1rem;
    display:flex;
}
.rating-star{
    color:#d5ab55;
}

.total-reviews{
    font-size: 0.9rem;
    margin-left: 10px;
}
.card-price{
    display:flex;
    justify-content: space-around;
    align-items: center;
}
.bag-icon{
    color: #535353;
}